.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }
    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo--container {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    column-gap: 24px;
    margin-bottom: 28px;

    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    width: 100%;
    grid-column: 2 / 4;
    border-radius: 50%;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.05);
    }

  }

  &__name {
    margin-bottom: 0 0 4px;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
  }

  &__role {
    line-height: 27px;
    font-size: 14px;
    text-align: center;
    color: #9fadc2;
    margin-bottom: 0 0 4px;
  }

  &__about {
    text-align: center;
  }
}

.link {
  color: $color-blue;
  text-decoration: none;
}
